@import url(https://fonts.googleapis.com/css?family=Arizonia);
@import url(https://fonts.googleapis.com/css?family=Akronim);

* {
  margin: 0;
  padding: 0;
}
.glavniy {
  background: url(./img/rasm.png);
  width: 100%;
  height: 625px;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.text-style {
  font-family: 'Arizonia' ;
  font-style: normal;
  font-weight: normal;
  font-size: 100px;
  line-height: 77px;
  /* or 89% */

  text-align: center;
  letter-spacing: 0.11em;

  color: #ffffff;
}
.big{
  font-size: 200px;
  padding-bottom: 30px;
  font-family: 'Akronim';
}
.small{
    font-size: 75px;
}
/* ---- reset ---- */
body {
    margin: 0;
    font: normal 75% Arial, Helvetica, sans-serif;
  }
  canvas {
    display: block;
    vertical-align: bottom;
  }
  /* ---- tsparticles container ---- */
  #tsparticles {
    position: absolute;
    width: 100%;
    height: 100%;
    // background-color: #0d41a1;
    background-image: url("");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
  }
  
  .github {
    bottom: 10px;
    right: 10px;
    position: fixed;
    border-radius: 10px;
    background: rgb(0, 0, 0);
    padding: 0 12px 6px 12px;
    border: 1px solid rgb(0, 0, 0);
  }
  
  .github a:hover,
  .github a:link,
  .github a:visited,
  .github a:active {
    color: rgb(0, 0, 0);
    text-decoration: none;
  }
  
  .github img {
    height: 30px;
  }
  
  .github #gh-project {
    font-size: 20px;
    padding-left: 5px;
    font-weight: bold;
    vertical-align: bottom;
  }

.Navbar{
  position: absolute;
  z-index: 1;
  display: flex;
  right: 15px;
  top: 10px;
}
.Navbar a{
  text-decoration: none;
  color: #ffffff;
  font-size: 20px;
  margin-left: 15px;
}
.Navbar a:hover{
  text-decoration: inherit;
  font-weight: bold;
}
