.gv {
  width: 100%;
  height: 625px;
  // background-color: rgb(0, 140, 255);
  padding-top: 25px;
}
.text-gold {
  font-family: Roboto;
  font-style: normal;
  font-weight: 900;
  font-size: 50px;
  line-height: 55px;
  color: #002b3e;

  text-align: center;
}
.card {
  display: flex;
  align-items: center;
  margin: 0 auto;
  height: 400px;
  width: 70%;
  border-radius: 50px;
  background: #f4f5f6;
}
img {
  height: 400px;
}
pr {
  margin-left: 10px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 34px;
  /* or 144% */

  letter-spacing: 0.06em;

  color: #000000;
}
.m-t {
  margin-top: 30px;
}
.loong {
  margin-top: 100px;
  padding: 15px;
  width: 70% !important;
}
.loong img {
  width: 400px;
}
.hero {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 50px;
  background: #f4f5f6;
  margin: 100px auto;
  // height: 500px;
  padding: 20px;
  width: 70% !important;

  background: #f4f5f6;
}
.hero a {
  
  font-size: 20px;
  text-decoration: none;
  color:#5a5a5a;
  ;
}
.hero a:hover {
  font-weight: bold;
  color:red;
}
.hero img {
  border-radius: 55px;
}
.hero p {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 18px;

  color: #576d81;
}
.hero h1 {
  font-family: Roboto;
  font-style: normal;
  font-weight: 900;
  font-size: 40px;
  line-height: 55px;
  /* identical to box height, or 138% */

  color: #002b3e;
}
